.nut-tab {
  padding: 0;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
  opacity: 1;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
@-webkit-keyframes imgShow {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes imgShow {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}



