
.recommend {
  margin-top: 10px;
}
.recommend .title {
  height: 35px;
  background: url("//img11.360buyimg.com/jdphoto/jfs/t1/31601/22/15554/14040/5cc2a86fEbdb1098b/88174b36f85283b6.png") no-repeat;
  background-size: 100%;
}
.recommend .recommonend-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 1.5%;
}
.recommend .recommonend-wrap .recommonend-item {
  margin-bottom: 6px;
  background-color: #fff;
  width: 49%;
}
.recommend .recommonend-wrap .recommonend-item p {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 31px;
  font-size: 13px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  word-break: break-word;
  color: #232326;
  margin-top: 5px;
  line-height: 16px;
  margin-bottom: 3px;
  padding: 0 4px;
}
.recommend .recommonend-wrap .recommonend-item img {
  width: 100%;
}
.recommend .recommonend-wrap .recommonend-item .item-bottom {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 10px 0;
}
.recommend .recommonend-wrap .recommonend-item .item-bottom .price {
  color: #f23030;
  display: inline-block;
  padding: 0 5px 0 4px;
  position: relative;
  top: 1px;
  height: 25px;
  line-height: 25px;
  font-size: 0;
}
.recommend .recommonend-wrap .recommonend-item .item-bottom .price i {
  font-size: 13px;
}
.recommend .recommonend-wrap .recommonend-item .item-bottom .price span {
  font-size: 16px;
}
.recommend .recommonend-wrap .recommonend-item .item-bottom .tag {
  border: 1px solid #e4393c;
  color: #e4393c;
  font-size: 10px;
  padding: 0 3px;
  margin-right: 5px;
}
.recommend .recommonend-wrap .recommonend-item .item-bottom .look {
  display: block;
  position: absolute;
  top: 0;
  right: 8px;
  text-align: center;
  color: #686868;
  font-size: 12px;
  width: 49px;
  height: 24px;
  line-height: 25px;
}
.recommend .recommonend-wrap .recommonend-item .item-bottom .look::after {
  content: "";
  width: 200%;
  height: 200%;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #bfbfbf;
  border-radius: 4px;
  -webkit-transform: scale(0.5, 0.5);
  transform: scale(0.5, 0.5);
  -webkit-transform-origin: top left;
  transform-origin: top left;
}



