
.countdown {
  margin: 10px;
  background: #fff;
}
.countdown .top-bar {
  position: relative;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
}
.countdown .top-bar .seckill-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.countdown .top-bar .seckill-wrap .tit-img {
  display: inline-block;
  width: 69px;
  height: 27px;
  margin-top: 8px;
  margin-right: 6px;
  margin-left: 0.45rem;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
}
.countdown .top-bar .seckill-wrap .sk-time {
  font-size: 12px;
  color: #232326;
  font-weight: 700;
  display: inline-block;
  margin-right: 6px;
}
.countdown .top-bar span.nut-cd-block {
  width: 18px;
  height: 16px;
  line-height: 17px;
  color: #232326;
  font-size: 12px;
  position: relative;
  margin: 0 1px;
}
.countdown .top-bar span.nut-cd-block::after {
  border-radius: 2px;
  height: 200%;
  content: "";
  width: 200%;
  border: 1px solid #dfdfdf;
  position: absolute;
  top: -1px;
  left: -1px;
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
  -webkit-transform-origin: left top;
  transform-origin: left top;
  z-index: 10;
}
.countdown .top-bar .more_seckill {
  position: absolute;
  right: 0;
  top: 0;
  display: inline-block;
  color: #f23030;
  font-size: 10px;
  text-align: right;
  padding-right: 22px;
  line-height: 32.5px;
}
.countdown .top-bar .seckill-more-icon {
  display: inline-block;
  width: 11px;
  height: 11px;
  background: url(//wq.360buyimg.com/fd/h5/wxsq_dev/m_jd_index/images/index/arrow_rt.png) no-repeat;
  background-size: cover;
  position: absolute;
  right: 8px;
  top: 11px;
}
.countdown .scroll-wrap {
  overflow: hidden;
}
.countdown .sk-items-wrap {
  width: 100%;
  font-size: 0;
  height: 120px;
  overflow: hidden;
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;
  padding-left: 5px;
  position: relative;
}
.countdown .sk-items-wrap .sk-items {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
}
.countdown .sk-items-wrap .sk-items .sk-item {
  width: 76.3441px;
  text-align: center;
}
.countdown .sk-items-wrap .sk-items .sk-item .img-wrap {
  padding: 0 5px;
}
.countdown .sk-items-wrap .sk-items .sk-item .price {
  margin-top: 10px;
  display: block;
  color: #e93b3d;
  line-height: 16px;
  height: 16px;
  font-weight: 700;
  text-align: center;
}
.countdown .sk-items-wrap .sk-items .sk-item .price i {
  font-size: 11px;
}
.countdown .sk-items-wrap .sk-items .sk-item .price span {
  font-size: 16px;
}
.countdown .sk-items-wrap .sk-items .sk-item .origin-price {
  text-decoration: line-through;
  color: #999;
  line-height: 12px;
  margin: 4px 0 11px;
  text-align: center;
  padding: 0 2px;
  display: inline-block;
  position: relative;
}
.countdown .sk-items-wrap .sk-items .sk-item .origin-price i,
.countdown .sk-items-wrap .sk-items .sk-item .origin-price span {
  font-size: 12px;
}
.countdown .sk-items-wrap .sk-items .sk-item img {
  width: 100%;
}



